
h1[data-v-3]{color:#42b983
}
.red[data-v-3]{color:red
}
.list[data-v-3]{width:50%;font-size:1rem;text-align:left;margin-left:auto;margin-right:auto
}
.list .item[data-v-3]:not(.active){color:gray
}
.list .item:not(.active) a[data-v-3]{color:#aca !important
}
.list .item.active a[data-v-3]{text-decoration:underline !important
}
.list .item input[data-v-3]{margin-right:5px
}
.right.bar[data-v-3]{position:fixed;top:50px;right:0;bottom:0;width:5px;background-color:green;background-image:linear-gradient(red, #ff0, green);background-image:-moz-linear-gradient(red, #ff0, green)
}
.right.bar .door[data-v-3]{position:fixed;z-index:1;top:50px;background-color:white;width:100%
}



